import React from "react";
import {connect} from "react-redux";
import {getPokemonDetail} from "../../../redux/actions";
import Types from "../../Smarts/Types/Types";
import s from './pokemonDetails.module.css';

class PokemonDetails extends React.Component {
  constructor(props){
    super(props);
    this.state = {loading: true};
  }

  componentDidMount() {
    try{
      this.props.getPokemonDetail(this.props.match.params.id).then(response => this.setState({loading: false}));
    }
    catch(err){
      console.log(err);
    }
  }

  render() {
    if (this.state.loading) return (<h3>Loading...</h3>);
    console.log(this.props);

    return (
      <div style={{width: '100%', textAlign: 'center'}}>
        <div style={{width: '70%', textAlign: 'center', backgroundColor: 'greenyellow', margin: 'auto'}}>
          <h1>{this.props.pokemonDetail.name[0].toUpperCase() + this.props.pokemonDetail.name.substring(1) + ` (${this.props.pokemonDetail.id})`}</h1>
          <img className={s.imgDetail} src={this.props.pokemonDetail.image} alt={this.props.pokemonDetail.name[0].toUpperCase() + this.props.pokemonDetail.name.substring(1)} />
          <Types types={this.props.pokemonDetail.types.map(name => ({name}))} actives={this.props.pokemonDetail.types.map(name => ({name}))} />

          
        </div>
      </div>
      );
  }
}

export default connect(({pokemonDetail, types}) => ({pokemonDetail, types}), {getPokemonDetail})(PokemonDetails);